import * as React from "react";
import { styled } from "@mui/material/styles";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Checkbox from "@mui/material/Checkbox";
import IconButton from "@mui/material/IconButton";
//import CommentIcon from "@mui/icons-material/Comment";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import LockIcon from "@mui/icons-material/Lock";
import LockOpenIcon from "@mui/icons-material/LockOpen";
import DeleteIcon from "@mui/icons-material/Delete";
import ArrowUpwardIcon from "@mui/icons-material/ArrowUpward";
import ArrowDownwardIcon from "@mui/icons-material/ArrowDownward";
import FlipToFrontIcon from "@mui/icons-material/FlipToFront";
import FlipToBackIcon from "@mui/icons-material/FlipToBack";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import Typography from "@mui/material/Typography";

const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.palette.common.white,
    color: "rgba(0, 0, 0, 0.87)",
    boxShadow: theme.shadows[1],
    fontSize: 16,
  },
}));

export default function LayerList({
  objList,
  setSelectedShape,
  setShapeVisiblity,
  setShapeLocking,
  bringShapeForward,
  sendShapeBackward,
  bringShapeFront,
  sendShapeBack,
  deleteShapeLayer,
  updateLayerSelection,
  updateMultiLayerSelection,
  currentlySelected,
}) {
  const [checked, setChecked] = React.useState([0]);
  //const [layerItmes, setLayerItems] = React.useState([]);
  //const [layerVisible, setLayerVisibility] = React.useState(true);
  const [hiddenLayers, setHiddenLayers] = React.useState([]);
  const [lockedLayers, setLockedLayers] = React.useState([]);
  const spaceString = "  ";
  React.useEffect(() => {
    const newLocked = [...lockedLayers];
    const newHidden = [...hiddenLayers];

    objList.forEach((value) => {
      if (!value.selectable) {
        newLocked.push(value);
      }
      if (!value.visible) {
        newHidden.push(value);
      }
    });
    setLockedLayers(newLocked);
    setHiddenLayers(newHidden);
  }, []);
  // console.log(objList);
  React.useEffect(() => {
    const newLocked = [];
    const newHidden = [];
    objList.forEach((value) => {
      if (!value.selectable) {
        newLocked.push(value);
      }
      if (!value.visible) {
        newHidden.push(value);
      }
    });
    setLockedLayers(newLocked);
    setHiddenLayers(newHidden);
    updateLayerSelection.current = updateListSel;
    updateMultiLayerSelection.current = updateMultiListSel;
    //  console.log(objList);
  }, [checked]);

  React.useEffect(() => {
    // console.log(objList);
    const currentIndex = checked.indexOf(currentlySelected);
    const newChecked = [0];
    if (currentIndex === -1) {
      newChecked.push(currentlySelected);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
    // console.log(objList);
  }, [currentlySelected]);
  // const { layerItmes, setSelectedShape } = props;
  //if (objList === undefined) return null;
  // console.log(layerItmes);

  function updateListSel(selectedObject) {
    //console.log(checked.length);
    //console.log(objList);
    if (selectedObject === null) {
      setChecked([0]);
      return;
    }
    objList.forEach(function (item) {
      if (item.id === selectedObject.id) {
        console.log(item);
        const currentIndex = checked.indexOf(item);

        if (checked.length > 2) {
          var newChecked = [...checked];
          console.log(checked.length);
        } else newChecked = [0];
        if (currentIndex === -1) {
          newChecked.push(item);
        } else {
          if (checked.length > 1) {
            //console.log(newChecked);
            newChecked.splice(currentIndex, 1);
            //console.log(newChecked);
          } else return;
          //
        }
        setChecked(newChecked);
      }
    });
  }
  function updateMultiListSel(selectedObject) {
    // console.log(objList);
    //console.log(selectedObject);
    if (selectedObject === null) {
      console.log(selectedObject);
      setChecked([0]);
      return;
    }
    if (selectedObject.length === 1) {
      setChecked(selectedObject);
      console.log(selectedObject);
      //updateListSel(selectedObject);
      return;
    }

    console.log(selectedObject);
    // rectangular box selection going on
    if (selectedObject.length > 1) {
      setChecked(selectedObject);
      return;
    }
    if (selectedObject._objects.length === 2)
      // shift+selection going on
      var shiftChecked = checked.slice(2);
    else shiftChecked = [0];

    //console.log(checked);
    selectedObject._objects.forEach((item) => {
      var currentIndex = shiftChecked.indexOf(item);
      if (currentIndex === -1) {
        shiftChecked.push(item);
      } else {
        shiftChecked.splice(currentIndex, 1);
      }
    });
    setChecked(shiftChecked);
  }
  const handleVisibility = (value) => () => {
    //console.log(value);
    //   if (checked.length > 1)
    //   {
    //       const indexFromSelected = checked.indexOf(value);
    //       if (indexFromSelected !== -1)
    //       {

    //           }
    //       }
    const currentHddenLayers = [...hiddenLayers];
    const currentIndex = hiddenLayers.indexOf(value);
    if (currentIndex === -1) {
      currentHddenLayers.push(value);
      setShapeVisiblity(value, false);
      //console.log(value);
    } else {
      currentHddenLayers.splice(currentIndex, 1);
      setShapeVisiblity(value, true);
    }
    setHiddenLayers(currentHddenLayers);
    // console.log(currentHddenLayers);
  };

  const hideSelected = () => {
    //console.log(checked);
    setShapeVisiblity(checked, false);
    var newHidden = [...hiddenLayers];
    for (var i = 1; i < checked.length; i++) {
      if (newHidden.indexOf(checked[i]) === -1) newHidden.push(checked[i]);
    }
    //console.log(hiddenLayers);
    setHiddenLayers(newHidden);
  };

  const showSelected = () => {
    //console.log(checked);
    setShapeVisiblity(checked, true);
    var newHidden = [...hiddenLayers];
    for (var i = 1; i < checked.length; i++) {
      var currentIndex = newHidden.indexOf(checked[i]);
      if (currentIndex !== -1) newHidden.splice(currentIndex, 1);
    }
    setHiddenLayers(newHidden);
  };

  const lockSelected = () => {
    //console.log(checked);
    setShapeLocking(checked, true);
    const newLocked = [...lockedLayers];
    for (var i = 1; i < checked.length; i++) {
      if (newLocked.indexOf(checked[i]) === -1) newLocked.push(checked[i]);
    }
    setLockedLayers(newLocked);
  };

  const unlockSelected = () => {
    //console.log(checked);
    setShapeLocking(checked, false);
    const newLocked = [...lockedLayers];
    for (var i = 1; i < checked.length; i++) {
      var currentIndex = newLocked.indexOf(checked[i]);
      if (currentIndex !== -1) newLocked.splice(currentIndex, 1);
    }
    setLockedLayers(newLocked);
  };

  const sendBackwardSelected = () => {
    sendShapeBackward(checked);
  };

  const bringForwardSelected = () => {
    bringShapeForward(checked);
  };

  const sendBackSelected = () => {
    sendShapeBack(checked);
  };

  const bringFrontSelected = () => {
    bringShapeFront(checked);
  };

  const deleteSelected = () => {
    deleteShapeLayer(checked);
  };

  const handleLocking = (value) => () => {
    // console.log(objList);
    const currentLockedLayers = [...lockedLayers];
    const currentIndex = lockedLayers.indexOf(value);
    if (currentIndex === -1) {
      currentLockedLayers.push(value);
      setShapeLocking(value, true);
    } else {
      currentLockedLayers.splice(currentIndex, 1);
      setShapeLocking(value, false);
    }
    setLockedLayers(currentLockedLayers);
  };

  const handleSelection = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
    setSelectedShape(newChecked);
  };

  const handleBringForward = (value) => () => {
    bringShapeForward(value);
  };
  const handleBringFront = (value) => () => {
    bringShapeFront(value);
  };
  const handleSendBackward = (value) => () => {
    sendShapeBackward(value);
  };
  const handleSendBack = (value) => () => {
    sendShapeBack(value);
  };
  const handleDelete = (value) => () => {
    deleteShapeLayer(value);
  };

  return (
    <>
      <List
        sx={{
          width: "100%",
          maxWidth: 330,
          bgcolor: "background.paper",
          position: "relative",
          overflow: "auto",
          maxHeight: 500,
          "& ul": { padding: 0 },
        }}
      >
        {objList.map((value, index) => {
          const labelId = `checkbox-list-label-${value.id}`;

          //console.log(value);
          return (
            <ListItem
              key={value.id}
              secondaryAction={
                <>
                  <LightTooltip
                    title={hiddenLayers.indexOf(value) === -1 ? "Hide" : "Show"}
                  >
                    <IconButton
                      edge="end"
                      aria-label="area-visiblity"
                      onClick={handleVisibility(value)}
                    >
                      {hiddenLayers.indexOf(value) === -1 ? (
                        <VisibilityIcon />
                      ) : (
                        <VisibilityOffIcon />
                      )}
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip
                    title={
                      lockedLayers.indexOf(value) === -1 ? "Lock" : "Unlock"
                    }
                  >
                    <IconButton
                      edge="end"
                      aria-label="area-lock"
                      onClick={handleLocking(value)}
                    >
                      {lockedLayers.indexOf(value) !== -1 ? (
                        <LockIcon />
                      ) : (
                        <LockOpenIcon />
                      )}
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip title="Bring Forward">
                    <IconButton
                      edge="end"
                      aria-label="area-bring-forward"
                      onClick={handleBringForward(value)}
                    >
                      <ArrowUpwardIcon />
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip title="Send Backward">
                    <IconButton
                      edge="end"
                      aria-label="area-send-backword"
                      onClick={handleSendBackward(value)}
                    >
                      <ArrowDownwardIcon />
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip title="Bring to Front">
                    <IconButton
                      edge="end"
                      aria-label="area-bring-to-front"
                      onClick={handleBringFront(value)}
                    >
                      <FlipToFrontIcon />
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip title="Send to Back">
                    <IconButton
                      edge="end"
                      aria-label="area-send-to-back"
                      onClick={handleSendBack(value)}
                    >
                      <FlipToBackIcon />
                    </IconButton>
                  </LightTooltip>
                  <LightTooltip title="Delete">
                    <IconButton
                      edge="end"
                      aria-label="area-delete"
                      onClick={handleDelete(value)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </LightTooltip>
                </>
              }
              disablePadding
            >
              <ListItemButton
                role={undefined}
                onClick={handleSelection(value)}
                dense
              >
                <ListItemIcon>
                  <Checkbox
                    edge="start"
                    checked={checked.indexOf(value) !== -1}
                    tabIndex={-1}
                    disableRipple
                    inputProps={{ "aria-labelledby": labelId }}
                  />
                </ListItemIcon>

                <ListItemText
                  id={labelId}
                  primary={index + 1}
                  secondary={value.name}
                  primaryTypographyProps={{
                    fontSize: "12px",
                  }}
                  secondaryTypographyProps={{
                    fontSize: "14px",
                  }}
                />
              </ListItemButton>
            </ListItem>
          );
        })}
      </List>
      <LightTooltip title="Hide Selected Layers">
        <IconButton edge="end" aria-label="hideSelected" onClick={hideSelected}>
          [<VisibilityOffIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Show Selected Layers">
        <IconButton edge="end" aria-label="showSelected" onClick={showSelected}>
          [<VisibilityIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Lock Selected Layers">
        <IconButton edge="end" aria-label="lockSelected" onClick={lockSelected}>
          [<LockIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Unlock Selected Layers">
        <IconButton
          edge="end"
          aria-label="unlockSelected"
          onClick={unlockSelected}
        >
          [<LockOpenIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Send Selected Layers Backward">
        <IconButton
          edge="end"
          aria-label="backwardSelected"
          onClick={sendBackwardSelected}
        >
          [<ArrowDownwardIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Bring Selected Layers Forward">
        <IconButton
          edge="end"
          aria-label="forwardSelected"
          onClick={bringForwardSelected}
        >
          [<ArrowUpwardIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Send Selected Layers to Back">
        <IconButton
          edge="end"
          aria-label="backSelected"
          onClick={sendBackSelected}
        >
          [<FlipToBackIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Bring Selected Layers to Front ">
        <IconButton
          edge="end"
          aria-label="frontSelected"
          onClick={bringFrontSelected}
        >
          [<FlipToFrontIcon />]
        </IconButton>
      </LightTooltip>
      <LightTooltip title="Delete Selected Layers">
        <IconButton
          edge="end"
          aria-label="deleteSelected"
          onClick={deleteSelected}
        >
          [<DeleteIcon />]
        </IconButton>
      </LightTooltip>
    </>
  );
}
